.home-container {
    text-align: center;
    padding: 20px;
    font-family: "Georgia", serif;
}

.home-header {
    font-size: 48px;
    color: #ff4a4a;
    margin-top: 20px;
    margin-bottom: 30px;
}

.search-section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.search-box {
    display: flex;
    align-items: center;
    border: 3px solid #ff4a4a;
    padding: 10px;
    border-radius: 10px;
    background: #eee;
    width: 350px;
}

.search-box input {
    border: none;
    background: transparent;
    width: 100%;
    font-size: 18px;
    outline: none;
}

.search-icon {
    font-size: 28px;
    margin-right: 10px;
    color: #ff4a4a;
}

.search-button {
    background: #ff4a4a;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 20px;
    border-radius: 10px;
    cursor: pointer;
    font-family: "Georgia", serif;
}

.recent-title {
    font-size: 36px;
    margin-top: 40px;
    color: #ff4a4a;
}

.divider {
    width: 90%;
    border: none;
    border-top: 3px solid #ff4a4a;
    margin: 10px auto 20px;
}

.login-warning {
    color: red;
    font-size: 20px;
}

.recent-list {
    list-style: none;
    padding: 0;
}

.recent-item {
    background: #f2f2f2;
    border: 2px solid #ccc;
    padding: 10px 20px;
    margin: 8px 0;
    font-size: 18px;
    border-radius: 8px;
    cursor: pointer;
}

.home-links {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.home-links a {
    color: black;
    text-decoration: none;
    font-size: 18px;
}